import React from 'react'
import { Container, Image, Row, Col, Card } from 'react-bootstrap'
import '../App.css'

function Skills() {
  const skills = [
    {
      name: 'Html',
      img: './img/skills/html5.svg',
    },
    {
      name: 'Css',
      img: './img/skills/css3.svg',
    },
    {
      name: 'JavaScript',
      img: './img/skills/javascript.svg',
    },
    {
      name: 'jquery',
      img: './img/skills/jquery.svg',
    },
    {
      name: 'git',
      img: './img/skills/git.svg',
    },
    {
      name: 'Sass',
      img: './img/skills/sass.svg',
    },
    {
      name: 'React',
      img: './img/skills/react.svg',
    },
  ]

  return (
    <Container className='style' id='skills'>
      <Row style={{ justifyContent: 'space-between', width: '100%' }}>
        <Col style={{ maxWidth: '28rem' }}>
          <h3>I'M A FRONT END DEVELOPER</h3>
          <p>Very passionate and dedicated to my work I have good experience to make your project a success </p>
          <div>I have a good knowledge about:</div>
          <Row>
            {skills.map((skill, key) => (
              <Col key={key} lg={'4'} md={'4'} xs={'4'} style={{ margin: '1rem 0', width: 'fit-content' }}>
                <Card>
                  <Card.Header>{skill.name}</Card.Header>
                  <div style={{ height: '30px', backgroundImage: 'url(' + skill.img + ')', backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'contain' }} />
                </Card>
              </Col>
            ))}
          </Row>
        </Col>
        <Col lg='5' md='4' style={{ flexGrow: '1' }}>
          <Image fluid src='./img/Untitled-5.png' className='no-drag' width={'600rem'} />
        </Col>
      </Row>
    </Container>
  )
}

export default Skills
