import React from 'react'
import { Container, Row, Col, Card, Button } from 'react-bootstrap'
function Contact() {
  return (
    <div className='bg-dark text-light'>
      <Container id='contact' className='pt-4 pb-5'>
        <Row>
          <Col>
            CONTACT WITH ME:
            <hr style={{ background: '#fff', marginLeft: '-1rem', maxWidth: '18rem' }} />
            <div className='mb-4'>GMAIL: mjnead2001@gmail.com</div>
            <div className='mb-4'>PHONE NUMBER: +963 951 217 157</div>
            <div className='mb-4'>LOCATION: Damascus, Syria</div>
          </Col>
          <Col>
            <Card bg='dark' className='p-3'>
              <Card.Title>MY RESUME</Card.Title>
              <Card.Body>
                <Button variant='light' href="./Mahmod Jnead's Resume.pdf">
                  DOWNLOAD TO VIEW
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default Contact
