import React from 'react'
import { Card, Col, Container, Image, Row } from 'react-bootstrap'
import '../App.css'

function Projects() {
  const projects = [
    {
      link: './loginPage',
      img: './img/projects/loginPage.png',
      tools: ['Html', 'Css'],
    },
    {
      link: './agency',
      img: './img/projects/agency.png',
      tools: ['Html', 'Sass'],
    },
    {
      link: './todolist',
      img: './img/projects/todolist.png',
      tools: ['React'],
    },
    {
      link: './new1',
      img: './img/projects/new1.png',
      tools: ['React'],
    },
  ]
  return (
    <>
      <Container className='style' style={{ flexDirection: 'column' }} id='projects'>
        <Row style={{ alignSelf: 'flex-start', paddingLeft: '15px', marginBottom: '20px' }}>My projects:</Row>
        <Row>
          {projects.map((project, key) => (
            <Col key={key} lg={'3'} md={'4'} style={{ marginBottom: '2rem', width: 'fit-content' }}>
              <Card>
                <Card.Body>
                  <a href={project.link} target='_blank' rel='noopener noreferrer'>
                    <Image className='hover no-drag' fluid src={project.img} />
                  </a>
                </Card.Body>
                <Card.Footer style={{ padding: 0 }}>
                  {project.tools.map(tool => (
                    <Card key={tool} style={{ width: 'fit-content', margin: '2px 5px', padding: '2px 5px', display: 'inline-block' }}>
                      {tool}
                    </Card>
                  ))}
                </Card.Footer>
              </Card>
            </Col>
          ))}
        </Row>
      </Container>
    </>
  )
}

export default Projects
