import React from 'react'
import { Col, Container, Image, Row } from 'react-bootstrap'
import '../App.css'

function Home() {
  return (
    <Container className='style' id='home'>
      <Row>
        <Col md={{ span: '6', order: 'first' }} xs={{ order: 'last' }}>
          <Image fluid src='./img/Coder.png' className='no-drag' width='600rem' />
        </Col>
        <Col>
          <div style={{ textAlign: 'center', marginBottom: '40px' }}>
            <h1 style={{ fontSize: '4.2rem' }}>HELLO</h1>
            <h3 style={{ letterSpacing: '3px' }}>MY NAME IS</h3>
            <h3 style={{ fontWeight: '300' }}>MAHMOD JNEAD</h3>
          </div>
        </Col>
      </Row>
    </Container>
  )
}

export default Home
